import React from 'react'

class Schedule extends React.Component {
    constructor(props) {
        super(props);
        this.ismounted = 1;
        this.state = {
            id: 4,
            ismounted: 1,
        };
        console.log('----> constructor TodoApp');
    }

    componentDidMount() {
        console.log('----> TodoApp componentDidMount')
    }

    componentWillUnmount() {
        console.log('----> TodoApp componentWillUnmount')
    }

    componentDidUpdate() {
        console.log('----> TodoApp componentDidUpdate')
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        console.log('----> getDerivedStateFromProps TodoApp');
        console.log('nextProps', nextProps, 'prevState', prevState);
        return prevState;
    }

    handleHide = () => {
        this.setState({
            ismounted: !this.state.ismounted,
        })
    };

    handleClickTodoApp = () => {
        this.setState({
            id: this.state.id + 1,
        })
    };

    render() {
        return (
            <div>
                <span onClick={this.handleClickTodoApp}>TodoApp {this.state.id}</span>
                <OtherComponent toOther={this.state.id}/>
                {this.state.ismounted && <OtherComponent/>}
                <span onClick={this.handleHide}>hide</span>
            </div>
        )
    }
}

class OtherComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            text: "OtherComponent",
            id: props.toOther,
        };
        console.log('----> constructor OtherComponent');
    }

    componentDidMount() {
        console.log('----> OtherComponent componentDidMount')
    }

    componentDidUpdate() {
        console.log('----> OtherComponent componentDidUpdate')
    }

    componentWillUnmount() {
        console.log('----> OtherComponent componentWillUnmount')
    }

    handleClickOtherDiv = () => {
        this.setState({
            text: "OtherComponent update = " + this.state.id,
            id: this.state.id + 1,
        })
    };

    static getDerivedStateFromProps(nextProps, prevState) {
        console.log('----> getDerivedStateFromProps OtherComponent');
        console.log('nextProps', nextProps, 'prevState', prevState);
        return prevState;
    }

    render() {
        return (
            <div onClick={this.handleClickOtherDiv}>
                {this.state.text}
            </div>
        )
    }
}

export default Schedule;


