import styled from 'styled-components';

export const TableStyled = styled.table`
  
    color: #4a4a4a;
    font-weight: 400;
    text-align: center;
    min-width: 550px;
    border-collapse: collapse;

    caption {
      text-align: left;
      margin-bottom: 3px;
      font-size: 0.8rem;
      font-style: italic;
    }
    
    select {
      border: none;
      width: 100%;
      height: 100%;
    }
    
    div {
      width: 100%;
    }
    
    th {
      text-align: left;
    }
    
    tr:nth-child(even) {
      background-color: #fafafa
    }
    
    td, th {
      padding: 5px 10px;
      border: 1px solid #dbdbdb;
    }
    
`;



