import {createReducer} from "../helpers/reducerUtils";
import moment from "moment";

const SELECT_DATE = 'SELECT_DATE';

const initialState = {
    current: moment(),
};

export default createReducer(initialState, {
    SELECT_DATE : selectDate,
});

// State Reducers
function selectDate(state, action) {

    return {
        ...state,
        current: action.payload,
    };
}

// Page Actions
export function selectedDate(value) {
    return (dispatch) => {
        dispatch({type: SELECT_DATE, payload: value})
    }
}



