import {all} from 'redux-saga/effects'
import {watchFetchTimeList} from './timeList'
import {watchFetchServiceList} from './serviceList'
import {watchFetchClientsCards} from './clientsCards'
import {watchFetchClientVisits} from './clientsVisits'

// single entry point to start all Sagas at once
export default function* rootSaga() {
    yield all([
        watchFetchTimeList(),
        watchFetchServiceList(),
        watchFetchClientsCards(),
        watchFetchClientVisits(),
    ])
}



