import {createReducer, requestedResourse, requestedError} from '../helpers/reducerUtils'
import {FILTERED_CLIENTS_CARDS} from './clientsCards'

const REQUESTED_SERVICE_LIST = 'REQUESTED_SERVICE_LIST';
const REQUESTED_SERVICE_LIST_SUCCEEDED = 'REQUESTED_SERVICE_LIST_SUCCEEDED';
const REQUESTED_SERVICE_LIST_FAILED = 'REQUESTED_SERVICE_LIST_FAILED';
const FETCHED_SERVICE_LIST = 'FETCHED_SERVICE_LIST';
const SELECT_SERVICE_LIST = 'SELECT_SERVICE_LIST';

const initialState = {
    data: [],
    byDefault: {},
    loading: false,
    error: false,
};

export default createReducer(initialState, {
    REQUESTED_SERVICE_LIST: requestedResourse,
    REQUESTED_SERVICE_LIST_SUCCEEDED: requestedServiceListSuccess,
    REQUESTED_SERVICE_LIST_FAILED: requestedError,
    SELECT_SERVICE_LIST: selectedServiceList,
});

function requestedServiceListSuccess(state, action) {

    const data = action.payload.map(item => {
        return {value: item.id, label: item.title}
    });

    data.unshift({value: 'all', label: ''});

    return {
        ...state,
        data: data,
        byDefault: data[0],
        loading: false,
        error: false,
    };
}


// State Reducers
function selectedServiceList(state, action) {

    let result = {};
    for(let item of state.data) {
        if(item.value === +action.payload) result = item;
    }

    return {
        ...state,
        byDefault: result,
    };
}

// Page Action
export function fetchServiceList() {
    return (dispatch) => {
        dispatch({type: FETCHED_SERVICE_LIST})
    }
}

// Action Creators
export const requestServiceList = () => {
    return {type: REQUESTED_SERVICE_LIST}
};

export const requestServiceListSuccess = (data) => {
    return {type: REQUESTED_SERVICE_LIST_SUCCEEDED, payload: data}
};

export const requestServiceListError = () => {
    return {type: REQUESTED_SERVICE_LIST_FAILED}
};



export function selectServiceList(value) {
    return (dispatch) => {
        dispatch({type: SELECT_SERVICE_LIST, payload: value});
        dispatch({type: FILTERED_CLIENTS_CARDS, payload: value});
    }
}



