import styled from 'styled-components';


export const HeaderStyled = styled.header`
  height: 60px;
  background-color: #00d1b2;
`;

export const Wrapper = styled.div`
  max-width: 800px;
  margin: 0 auto;
  color: white;
  font-weight: bold;
  font-size: 40px;
  text-align: end;
`;



