import React from 'react'
import PropTypes from "prop-types";

const Row = ({data, crossed, onSelectRow, rowIndex}) => {
    const cells = [];
    for (let cell in crossed) {
        cells.push(<td key={cell}>{crossed[cell]}</td>)
    }

    return (
        <tr onClick={() => onSelectRow(data, rowIndex)}>
            {cells}
        </tr>
    );
};

Row.propTypes = {
    data: PropTypes.array,
    crossed: PropTypes.object,
    onSelectRow: PropTypes.func,
    rowIndex: PropTypes.number,
};
Row.defaultProps = {
    onSelectRow: () => null,
};
export default Row;




