import {createReducer} from "../helpers/reducerUtils";

const PUT_ALERT = 'PUT_ALERT';
const PUT_MESSAGE = 'PUT_MESSAGE';
const PUT_ERRORS = 'PUT_ERRORS';

const initialState = {
    errors: [],
    message: [],
};

export default createReducer(initialState, {
    CHANGE_MESSAGE: changedMessage,
    PUT_ERRORS: changedErrors,
});

function changedMessage(state, action) {
    return {
        ...state,
        message: state.text.push(action.payload)
    }
}

// State Reducers
function changedErrors(state, action) {
    return {
        ...state,
        errors: action.payload
    }
}


// Action Creators
export function messagePut(value) {
    return (dispatch) => {

        dispatch({type: PUT_MESSAGE, payload: value});
    }
}

export function messagePutError(value) {
    return (dispatch) => {
        console.log(value);
        dispatch({type: PUT_ERRORS, payload: value});
    }
}




