import moment from "moment";
import {compareTables} from '../helpers/dataHandler'

const CHANGE_MESSAGE = 'CHANGE_MESSAGE';
const REQUEST_SERVICE_LIST = 'REQUEST_SERVICE_LIST';
const CHANGE_CLIENT_VISIT_TABLE = 'CHANGE_CLIENT_VISIT_TABLE';
const CHANGE_CLIENT_CARD_TABLE = 'CHANGE_CLIENT_CARD_TABLE';
const FILTERING_CLIENT_CARD_TABLE = 'FILTERING_CLIENT_CARD_TABLE';
const CHANGE_SERVICE = 'CHANGE_SERVICE';
const CHANGE_DATE = 'CHANGE_DATE';
const REQUEST_TIME = 'REQUEST_TIME';
const CHANGE_TIME = 'CHANGE_TIME';
const SELECT_CLIENT_CARD_STATUS = 'SELECT_CLIENT_CARD_STATUS';
const SELECT_CLIENT_VISIT_STATUS = 'SELECT_CLIENT_VISIT_STATUS';
const REGISTER_CLIENT_VISIT = 'REGISTER_CLIENT_VISIT';

const initialState = {
    serviceList: [],
    serviceDefault: 'all',
    timeList: [],
    timeDefault: '',
    timeLabel: '',
    date: moment(),
    message: '',
    clientCardTable: [],
    clientCardTableFiltered: [],
    clientVisitTable: [],
};

export default function reducer(state = initialState, action) {
    switch (action.type) {
        case CHANGE_MESSAGE:

            return {
                ...state,
                message: action.payload,
            };

        case REQUEST_SERVICE_LIST:

            const serviceList = action.payload.map(item => {
                return {value: item.id, label: item.title}
            });

            serviceList.unshift({value: 'all', label: ''});

            return {
                ...state,
                serviceList: serviceList.map(item => Object.assign({}, item)),
            };

        case CHANGE_SERVICE:

            return {
                ...state,
                serviceDefault: action.payload,
            };

        case REQUEST_TIME:

            const timeList = action.payload.map(item => {
                return {value: item.id, label: item.title}
            });

            return {
                ...state,
                timeList: timeList,
                timeDefault: timeList[0]['value'],
                timeLabel: timeList[0]['label']
            };

        case CHANGE_TIME:

            let timeLabel = '';
            state.timeList.forEach((item) => {
                if (item.value === +action.payload) {
                    timeLabel = item.label;
                }
            });

            return {
                ...state,
                timeDefault: action.payload,
                timeLabel: timeLabel,
            };

        case CHANGE_DATE:

            return {
                ...state,
                date: action.payload,
            };

        case CHANGE_CLIENT_CARD_TABLE:

            return {
                ...state,
                clientCardTable: action.payload,
                clientCardTableFiltered: action.payload.map((item) => Object.assign({}, item)),
            };

        case REGISTER_CLIENT_VISIT:

            return {
                ...state,
                clientCardTable: action.payload.map(item => Object.assign({}, item)),
                clientCardTableFiltered: action.payload.map(item => Object.assign({}, item)),
                message: '  ',
                clientVisitTable: [
                    ...state.clientVisitTable,
                    ...action.payload.map(item => ({...item}))
                ],
            };

        case CHANGE_CLIENT_VISIT_TABLE:

            return {
                ...state,
                clientVisitTable: action.payload.res,
                date: action.payload.date,
            };

        case FILTERING_CLIENT_CARD_TABLE:

            const {field, value} = action.payload;

            let filteredCardTable = state.clientCardTable.filter(item => {
                if (item[field] === +value || value === 'all') return item;
            });

            return {
                ...state,
                serviceDefault: value,
                clientCardTableFiltered: filteredCardTable.map(item => Object.assign({}, item)),
            };

        case SELECT_CLIENT_CARD_STATUS: {

            const {status, row} = action.payload;

            const changedCardFilteredStatus = state.clientCardTableFiltered.map(item => {
                if (row['client_card_id'] === item['client_card_id']) {
                    return {...item, cltv_visit_status: status};
                }
                return item
            });

            return {
                ...state,
                clientCardTableFiltered: changedCardFilteredStatus,
            };

        }

        case SELECT_CLIENT_VISIT_STATUS: {

            // const {status, row} = action.payload;
            // let prevStatus = '';
            //
            // const clientVisitTable = state.clientVisitTable.map(item => {
            //     if (row['client_card_id'] === item['client_card_id'] && row['cltv_time'] === item['cltv_time']) {
            //         prevStatus = item.cltv_visit_status;
            //         return {...item, cltv_visit_status: status};
            //
            //     }
            //     return item
            // });
            //
            // const clientCardTableFiltered = state.clientCardTableFiltered.map(item => {
            //     if (row['client_card_id'] === item['client_card_id']) {
            //         return {...item, cltc_remainder: item.cltc_remainder + getUpdateStatus(prevStatus, status)};
            //     }
            //     return item
            // });

            return {
                ...state,
                clientVisitTable: action.payload.t1,
                clientCardTableFiltered: action.payload.t2,
            };

        }

        default:
            return state;
    }
};

export function actionChangeMessage(value) {
    return {
        type: CHANGE_MESSAGE,
        payload: value
    }
}

export function actionChangeServiceList(data) {
    // console.log('actionChangeServiceList', data);
    return {
        type: REQUEST_SERVICE_LIST,
        payload: data
    }
}

export function actionChangeClientVisitTable(data) {
    return {
        type: CHANGE_CLIENT_VISIT_TABLE,
        payload: data,
    }
}

export function actionChangeClientCardTable(data) {
    return {
        type: CHANGE_CLIENT_CARD_TABLE,
        payload: data,
    }
}

export function actionSelectService(field, value) {
    return {
        type: FILTERING_CLIENT_CARD_TABLE,
        payload: {field, value},
    }
}

export function actionSelectClientCardStatus(status, row) {
    return {
        type: SELECT_CLIENT_CARD_STATUS,
        payload: {status, row},
    }
}

export function actionUpdateClientVisitStatus(t1, t2) {
    return {
        type: SELECT_CLIENT_VISIT_STATUS,
        payload: {t1, t2}
    }
}

export function actionRequestTime(data) {
    return {
        type: REQUEST_TIME,
        payload: data,
    }
}

export function actionSelectTime(value) {
    return {
        type: CHANGE_TIME,
        payload: value,
    }
}

export function actionRegisterClientVisit(table) {
    return {
        type: REGISTER_CLIENT_VISIT,
        payload: table,
    }
}

export const updateClientVisitStatus = (status, row) => {
    return (dispatch, getState) => {

        const state = getState().clientvisit;
        let prevStatus = '';

        const clientVisitTable = state.clientVisitTable.map(item => {
            if (row['client_card_id'] === item['client_card_id'] && row['cltv_time'] === item['cltv_time']) {
                prevStatus = item.cltv_visit_status;
                return {...item, cltv_visit_status: status};

            }
            return item
        });

        const remainder = prevStatus - getUpdateStatus(row['cltv_visit_status'], status);

        const clientCardTableFiltered = state.clientCardTableFiltered.map(item => {
            if (row['client_card_id'] === item['client_card_id']) {
                return {...item, cltc_remainder: item.cltc_remainder + remainder};
            }
            return item
        });

        const result = {...row, cltc_remainder: remainder};

        const dataPost = new FormData();
        dataPost.append("client_visit", JSON.stringify(result));

        fetch('http://localhost:3000/admin/clientvisit/status', {
            method: 'PUT',
            body: dataPost,
        })
            .then(res => {
                console.log(res)
                if (res.ok) {
                    dispatch(actionUpdateClientVisitStatus(clientVisitTable, clientCardTableFiltered));
                }
            })
            .catch(err => {
                console.log('updateClientVisitStatus Fetch Error:', err);
            });
    }
};

function getUpdateStatus(prevStatus, nextStatus) {

    // console.log('prevStatus, nextStatus', prevStatus, nextStatus);

    const stateArr = [
        ["0", "1", 0],
        ["0", "2", 1],
        ["2", "0", -1],
        ["1", "0", 0],
        ["1", "2", 1],
        ["2", "1", -1],
    ];

    let state = 0;

    stateArr.forEach(item => {
        if (item[0] === String(prevStatus) && item[1] === String(nextStatus)) state = item[2];
    });

    return state;
}



