import {call, put, takeEvery} from 'redux-saga/effects'
import {requestServiceList, requestServiceListSuccess, requestServiceListError} from '../reducers/serviceList'
import {messagePutError} from "../reducers/message";
import * as Api from '../Api'

// Sagas
export function* watchFetchServiceList() {
    yield takeEvery('FETCHED_SERVICE_LIST', fetchServiceListAsync);
}

function* fetchServiceListAsync() {
    try {

        yield put(requestServiceList());

        const data = yield call(Api.fetchPath, '/service');

        yield put(requestServiceListSuccess(data));

    } catch (error) {

        yield put(messagePutError('Fetch error: ' + error));
        yield put(requestServiceListError());
    }
}



