import styled from 'styled-components';

export const Wrapper = styled.div`
    justify-content: center;
    display: flex;
    flex-direction: column;
`;

export const HandleGroup = styled.div`
    display: flex;
    flex-wrap: wrap;
    
    > * {
      margin-right: 10px;
      padding-left: 4px;
    }
`;

export const TableWrap = styled.div`

    > table {
      text-align: center;
      min-width: 550px;
      margin: 16px 0 16px 0;
    }
    
    > table[class="private-user-info"] tr:hover{
      cursor: pointer;
      background-color: aliceblue;
    }
    
    > table[class="private-user-info"] td:nth-child(n+3):nth-child(-n+4) input {
      width: 100px;
    }
    
    > table[class="private-user-info"] td:nth-child(n+5):nth-child(-n+6) input {
      width: 50px;
    }
    
    > table[class="bought-services"] td:first-child {
      width: 20px;
    }
    
    > table[class="bought-services"] td:nth-child(n+2):nth-child(-n+3) {
      text-align: left;
    }
    
    > table[class="bought-services"] td:nth-child(n+4):nth-child(-n+6) {
      width: 50px;
    }
    
    > table caption{
      text-align: left;
      margin-bottom: 3px;
      font-size: 14px;
      font-style: italic;
    }
    
    > table select{
      border: none;
      width: 100%;
    }
`;



