const REQUEST_CLIENT_INFO_TABLE = 'REQUEST_CLIENT_INFO_TABLE';
const REQUEST_CLIENT_CARD_TABLE = 'REQUEST_CLIENT_CARD_TABLE';
const CHANGE_FIELD_FINDBYNAME = 'CHANGE_FIELD_FINDBYNAME';
const CHANGE_SERVICE_LIST = 'CHANGE_CLIENT_INFO_SERVICE_LIST';
const SELECT_USER_ROW = 'SELECT_USER_ROW';
const CHANGE_FIELD = 'CHANGE_FIELD';
const CHANGE_INPUT = 'CHANGE_INPUT';
const ADD_NEW_USER = 'ADD_NEW_USER';
const ADD_NEW_TICKET = 'ADD_NEW_TICKET';
const DELETE_USER_ROW = 'DELETE_USER_ROW';
const DELETE_USER_CARD = 'DELETE_USER_CARD';
const RESET_FILTERING = 'RESET_FILTERING';

const initialState = {
    clientInfoTable: [],
    clientInfoTableFiltered: [],
    clientInfoTableFilteredPrev: [],
    clientCardTable: [],
    clientCardTableFiltered: [],
    findByName: '',
    serviceList: [],
    activeUserRow: {},
};

export default function reducer(state = initialState, action) {
    switch (action.type) {
        case REQUEST_CLIENT_INFO_TABLE:
            return {
                ...state,
                clientInfoTable: action.payload,
                clientInfoTableFiltered: action.payload,
            };
        case REQUEST_CLIENT_CARD_TABLE:
            return {
                ...state,
                clientCardTable: action.payload,
                clientCardTableFiltered: action.payload,
            };
        case CHANGE_FIELD_FINDBYNAME:
            let filteredUserByName = function () {
                return state.clientInfoTable.filter(item => {
                    let reg = new RegExp(action.payload, 'gi');
                    if (reg.test(item['usr_full_name'])) return item;
                });
            };
            let filteredCardByName = function () {
                return state.clientCardTable.filter(item => {
                    let reg = new RegExp(action.payload, 'gi');
                    if (reg.test(item['usr_full_name'])) return item;
                });
            };
            return {
                ...state,
                findByName: action.payload,
                clientInfoTableFiltered: filteredUserByName().map((item) => Object.assign({}, item)),
                clientCardTableFiltered: filteredCardByName().map((item) => Object.assign({}, item)),
            };

        case SELECT_USER_ROW:
            // console.log('SELECT_USER_ROW', action);
            let filteredCardByTargetName = function () {
                return state.clientCardTable.filter(item => {
                    if (item['usr_id'] === action.payload['usr_id']) return item;
                });
            };
            return {
                ...state,
                clientCardTableFiltered: filteredCardByTargetName().map((item) => Object.assign({}, item)),
                activeUserRow: action.payload,
            };
        case RESET_FILTERING:
            return {
                ...state,
                clientCardTableFiltered: state.clientCardTable,
                clientInfoTableFiltered: state.clientInfoTable,
                findByName: '',
                activeUserRow: {},
            };
        case ADD_NEW_USER:
            return {
                ...state,
                clientInfoTableFiltered: state.clientInfoTableFiltered.concat(action.payload)
            };
        case ADD_NEW_TICKET:
            return {
                ...state,
                clientCardTableFiltered: state.clientCardTableFiltered.concat(action.payload)
            };
        case CHANGE_INPUT:
            let changedInput = function() {
                const {id, dataIndex, payload} = action;
                return state.clientInfoTableFiltered.map((item) => {
                    if (item['usr_id'] === id) item[dataIndex] = payload;
                    return item;
                });
            };
            return {
                ...state,
                clientInfoTable: changedInput()
            };
        case DELETE_USER_ROW:
            let filteredUserTable = function() {
                return state.clientInfoTableFiltered.filter((item) => {
                    if (item['usr_id'] !== action.payload) return item;
                });
            };
            return {
                ...state,
                clientInfoTableFiltered: filteredUserTable()
            };
        case DELETE_USER_CARD:
            let filteredUserCard = function() {
                return state.clientCardTableFiltered.filter((item) => {
                    if (item['client_card_id'] !== action.payload) return item;
                });
            };
            return {
                ...state,
                clientCardTableFiltered: filteredUserCard()
            };
        case CHANGE_SERVICE_LIST:
            return {
                ...state,
                serviceList: action.payload.map((item) => Object.assign({}, item)),
            };
        default:
            return state;
    }
}

export function actionRequestClientInfoTable(data) {
    return {
        type: REQUEST_CLIENT_INFO_TABLE,
        payload: data,
    }
}

export function actionRequestClientCardTable(data) {
    return {
        type: REQUEST_CLIENT_CARD_TABLE,
        payload: data,
    }
}

export function actionChangeInput(id, dataIndex, value) {
    // console.log('actionChangeInput', id, dataIndex, value);
    return {
        type: CHANGE_INPUT,
        id: id,
        dataIndex: dataIndex,
        payload: value,
    }
}

export function actionChangeServiceList(data) {
    // console.log('actionChangeServiceList', data);
    return {
        type: CHANGE_SERVICE_LIST,
        payload: data
    }
}

export function actionSelectUserRow(data, rowIndex) {
    // console.log(data);
    return {
        type: SELECT_USER_ROW,
        payload: data[rowIndex],
    }
}

export function actionResetFiltering() {
    return {
        type: RESET_FILTERING,
    }
}

export function actionChangeFieldFindByName(value) {
    return {
        type: CHANGE_FIELD_FINDBYNAME,
        payload: value,
    }
}

export function actionDeleteUserRow(value) {
    return {
        type: DELETE_USER_ROW,
        payload: value,
    }
}

export function actionDeleteUserCard(value) {
    return {
        type: DELETE_USER_CARD,
        payload: value,
    }
}

export function actionAddNewTicket(newTicket) {
    // console.log('actionAddNewTicket', newTicket);
    return {
        type: ADD_NEW_TICKET,
        payload: newTicket,
    }
}

export const sendChangeInputUsers = (id, dataIndex, value) => {

    let indexConnector = {
        usr_id: 'id',
        usr_login: 'login',
        usr_email: 'email',
        usr_money: 'money',
        usr_full_name: 'full_name',
    };
    let field = {
        id: id,
        [indexConnector[dataIndex]]: value,
    };

    let dataPost = new FormData();
    dataPost.append('new_user', JSON.stringify(field));

    return (dispatch) => {
        fetch("http://localhost/admin/updateClientInfoField", {
            method: 'POST',
            body: dataPost
        })
            .then(res => res.json())
            .then(res => dispatch(actionChangeInput(id, dataIndex, value)))
            .catch(err => {
                console.log('Fetch Error:', err);
            });
    }
};

export const sendDeleteUserRow = (id) => {

    let dataPost = new FormData();
    dataPost.append('user_id', JSON.stringify(id));

    return (dispatch) => {
        fetch("http://localhost/admin/deleteUserRow", {
            method: 'POST',
            body: dataPost
        })
            // .then(res => res.json())
            .then(res => {
                dispatch(actionDeleteUserRow(id))
            })
            .catch(err => {
                console.log('Fetch Error:', err);
            });
    }
};

export const sendDeleteUserCard = (id) => {

    let dataPost = new FormData();
    dataPost.append('card_id', JSON.stringify(id));

    return (dispatch) => {
        fetch("http://localhost/admin/DeleteUserCard", {
            method: 'POST',
            body: dataPost
        })
        // .then(res => res.json())
            .then(res => {
                dispatch(actionDeleteUserCard(id))
            })
            .catch(err => {
                console.log('Fetch Error:', err);
            });
    }
};

export const sendAddClientCardTicket = (newTicket) => {
    let dataPost = new FormData();
    dataPost.append('new_ticket', JSON.stringify(newTicket));

    return (dispatch) => {
        fetch("http://localhost/admin/addClientCardTicket", {
            method: "POST",
            body: dataPost
        })
            .then(res => res.json())
            .then(result => {
                console.log('sendAddClientCardTicket result', result);
                const {user, card, service} = result;
                let data = {
                    client_card_id: card.id,
                    cltc_count: card.count,
                    cltc_price: card.price,
                    cltc_remainder: card.remainder,
                    cltv_visit_status: null,
                    service_id: service.id,
                    srv_title: service.title,
                    time_of_purchase: card.time_of_purchase,
                    usr_full_name: user.full_name,
                    usr_id: user.id,
                };
                dispatch(actionAddNewTicket(data));
            })
            .catch(err => {
                console.log('Fetch Error:', err);
            })
    }
};

export const requestClientInfoTable = () => {
    return (dispatch) => {
        fetch('http://localhost/admin/clientinfotable')
            .then(res => res.json())
            .then(res => {
                dispatch(actionRequestClientInfoTable(res))
            })
            .catch(err => {
                console.log('Fetch Error:', err);
            });
    }
};

export const requestClientCardTable = () => {
    return (dispatch) => {
        fetch('http://localhost/admin/clientcardtable')
            .then(res => res.json())
            .then(res => dispatch(actionRequestClientCardTable(res)))
            .catch(err => {
                console.log('Fetch Error:', err);
            });
    }
};

export const requestServiceList = () => {
    return (dispatch) => {
        fetch("http://localhost/admin/servicelist")
            .then(res => res.json())
            .then(res => {
                dispatch(actionChangeServiceList(res));
            })
            .catch(err => {
                console.log('Fetch Error:', err);
            });
    }
};



