import styled from 'styled-components';

export const InputStyled = styled.input`
     padding: calc(.375em - 1px) calc(.625em - 1px);
     margin: 0;
     box-sizing: border-box; 
     border: 1px solid #dbdbdb;
     border-radius: 4px;
     height: 2.5em;
     font-size: 1em;
     line-height: 1.5em;
    
    &:focus {
        box-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
    }
`;



