import React, {Component} from "react";
import Select from '../Select'
import PropTypes from "prop-types";

class AddTicket extends Component {

    constructor(props) {
        super(props);

        this.state = {
            ticketCount: '',
            ticketPrice: '',
        }
    }

    handleChangeServiceList = (e) => {
        this.setState({
            serviceDefault: e.target.value,
        })
    };

    handleChangeTicketCount = (e) => {
        this.setState({
            ticketCount: e.target.value,
        })
    };
    handleChangeTicketPrice = (e) => {
        this.setState({
            ticketPrice: e.target.value,
        })
    };
    handleSendNewTicket = () => {
        const {ticketCount, ticketPrice} = this.state;
        const {activeUserRow, serviceDefault} = this.props;
        const newTicket = {
            serviceDefault: serviceDefault,
            ticketCount: ticketCount,
            remainder: ticketCount,
            ticketPrice: ticketPrice,
            activeUserRow: activeUserRow,
        };
        this.props.actionAddNewTicket(newTicket)
    };

    render() {
        const {serviceDefault, ticketCount, ticketPrice} = this.state;
        const {activeUserRow, serviceList} = this.props;
        return (
            Object.keys(activeUserRow).length !== 0 &&
            <div>
                <Select
                    data={serviceList}
                    defaultSelect={serviceDefault}
                    onSelectChange={(e) => this.handleChangeServiceList(e)}
                />
                <input
                    type="text"
                    placeholder=""
                    value={ticketCount}
                    onChange={(e) => this.handleChangeTicketCount(e)}
                />
                <input
                    type="text"
                    placeholder=""
                    value={ticketPrice}
                    onChange={(e) => this.handleChangeTicketPrice(e)}
                />
                <input
                    type="button"
                    value=" "
                    onClick={this.handleSendNewTicket}
                />
                <span>{activeUserRow['usr_full_name']}</span>
            </div>
        )
    }
}

AddTicket.propTypes = {
    userId: PropTypes.string,
    serviceList: PropTypes.array,
    activeUserRow: PropTypes.object,
};

AddTicket.defaultProps = {
    userId: '',
    serviceList: [],
    activeUserRow: {},
};

export default AddTicket;


