import {call, put, takeEvery} from 'redux-saga/effects'
import {requestTimeList, requestTimeListSuccess, requestTimeListError} from '../reducers/timeList'
import * as Api from '../Api'

// Sagas
export function* watchFetchTimeList() {
    yield takeEvery('FETCHED_TIME_LIST', fetchAsync);
}

function* fetchAsync() {
    try {

        yield put(requestTimeList());

        const data = yield call(Api.fetchPath, '/schedule/timelist');

        yield put(requestTimeListSuccess(data));

    } catch (error) {

        yield put(requestTimeListError());
    }
}



