export const createReducer = (initialState, handlers) => {
    return function reducer(state = initialState, action) {
        if (handlers.hasOwnProperty(action.type)) {
            return handlers[action.type](state, action)
        } else {
            return state
        }
    }
};

export const requestedResourse = (state, action) => {
    return {
        ...state,
        loading: true,
        error: false,
    }
};

export const requestedSuccess = (state, action) => {
    return {
        ...state,
        date: action.payload,
        loading: false,
        error: false,
    }
};

export const requestedError = (state, action) => {
    return {
        ...state,
        loading: false,
        error: true,
    };
};







