import {call, put, takeEvery} from 'redux-saga/effects'
import {
    requestClientsCards,
    requestClientsCardsSuccess,
    requestClientsCardsError
} from '../reducers/clientsCards'
import {FETCHED_CLIENTS_CARDS} from '../reducers/clientsCards'
import * as Api from '../Api'

// Sagas
export function* watchFetchClientsCards() {
    yield takeEvery(FETCHED_CLIENTS_CARDS, fetchClientCardsAsync);
}

function* fetchClientCardsAsync() {
    try {

        yield put(requestClientsCards());

        const data = yield call(Api.fetchPath, '/admin/clientcard');

        yield put(requestClientsCardsSuccess(data));

    } catch (error) {

        yield put(requestClientsCardsError());
    }
}



