import React, {Component} from 'react'
// import Table from '../../Table'
import AddTicket from "../../AddTicket"
import EditableCell from "../../EditableCell"
import Table from '../../Table'
import {Wrapper, TableWrap, HandleGroup} from "./styled"

import {connect} from 'react-redux'
import * as actions from "../../../store/reducers/client-info";
import {bindActionCreators} from "redux";


class ClientInfo extends Component {

    componentDidMount() {
        this.props.clientInfoTable.length === 0 && this.props.requestClientInfoTable();
        this.props.clientCardTable.length === 0 && this.props.requestClientCardTable();
        this.props.serviceList.length === 0 && this.props.requestServiceList();
    }

    columnsUsersTable = [
        {
            title: '',
            render: (column, row, row_index) => {
                return row_index + 1;
            }
        },
        {
            title: '',
            dataIndex: 'usr_full_name',
            render: (column, row) =>
                <EditableCell
                    id={row['usr_id']}
                    data={row[column.dataIndex]}
                    dataIndex={column.dataIndex}
                    actionChangeInput={this.props.sendChangeInputUsers}
                />
        },
        {
            title: '',
            dataIndex: 'usr_login',
            render: (column, row) =>
                <EditableCell
                    id={row['usr_id']}
                    data={row[column.dataIndex]}
                    dataIndex={column.dataIndex}
                    actionChangeInput={this.props.sendChangeInputUsers}
                />
        },
        {
            title: '',
            dataIndex: 'usr_email',
            render: (column, row) =>
                <EditableCell
                    id={row['usr_id']}
                    data={row[column.dataIndex]}
                    dataIndex={column.dataIndex}
                    actionChangeInput={this.props.sendChangeInputUsers}
                />
        },
        {
            title: '',
            dataIndex: 'usr_money',
            render: (column, row) =>
                <EditableCell
                    id={row['usr_id']}
                    data={row[column.dataIndex]}
                    dataIndex={column.dataIndex}
                    actionChangeInput={this.props.sendChangeInputUsers}
                />
        },
        {
            title: '',
            dataIndex: 'delete',
            render: (column, row) =>
                <a href="#" onClick={() => this.props.sendDeleteUserRow(row['usr_id'])}></a>
        },
    ];
    columnsCardTable = [
        {
            title: '',
            dataIndex: 'client_card_id',
        },
        {
            title: '',
            dataIndex: 'usr_full_name',
        },
        {
            title: '',
            dataIndex: 'srv_title',
        },
        {
            title: '',
            dataIndex: 'cltc_count',
        },
        {
            title: '',
            dataIndex: 'cltc_price',
        },
        {
            title: ' ',
            dataIndex: 'cltc_remainder',
        },
        {
            title: ' ',
            dataIndex: 'time_of_purchase',
        },
        {
            title: '',
            render: (column, row) =>
                <a href="#" onClick={() => this.props.sendDeleteUserCard(row['client_card_id'])}></a>
        },

    ];

    render() {
        const {
            findByName,
            clientInfoTableFiltered,
            actionChangeFieldFindByName,
            clientCardTableFiltered,
            sendAddClientCardTicket,
            actionSelectUserRow,
            actionResetFiltering,
            activeUserRow,
            serviceList,
        } = this.props;

        return (
            <Wrapper>
                <HandleGroup>
                    <input
                        type="text"
                        value={findByName}
                        placeholder="  "
                        onChange={e => actionChangeFieldFindByName(e.target.value)}
                    />
                    <input type="button" value=" " onClick={actionResetFiltering}/>
                </HandleGroup>
                <TableWrap>
                    <Table
                        caption="  "
                        columns={this.columnsUsersTable}
                        data={clientInfoTableFiltered}
                        onSelectRow={(data, rowIndex) => actionSelectUserRow(data, rowIndex)}
                        className={"private-user-info"}
                    />
                    <Table
                        caption="  "
                        columns={this.columnsCardTable}
                        data={clientCardTableFiltered}
                        onSelectRow={(e) => e.preventDefault}
                        className={"bought-services"}
                    />
                </TableWrap>
                <AddTicket
                    activeUserRow={activeUserRow}
                    serviceList={serviceList}
                    serviceDefault={serviceList.length !== 0 && serviceList[0]['value']}
                    actionAddNewTicket={sendAddClientCardTicket}
                />
            </Wrapper>
        )
    }
}

const mapStateToProps = (state) => state.clientinfo;
const mapDispatchToProps = (dispatch) => bindActionCreators(actions, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(ClientInfo);



