import React, {Fragment} from 'react';
import {Route} from 'react-router-dom'

import Schedule from '../../Schedule'
import Ticket from "../Ticket";
import ClientVisit from "../RegClientsVisit";
import ClientInfo from "../RegClientsInfo";

const Routs = () => {

    return (
        <Fragment>
            <Route path='/admin/clientvisit' component={ClientVisit}/>
            <Route path='/admin/clientinfo' component={ClientInfo}/>
            <Route path='/admin/schedule' component={Schedule}/>
            <Route path='/admin/ticket' component={Ticket}/>
        </Fragment>
    )
};

export default Routs;



