import {createReducer, requestedError, requestedResourse} from "../helpers/reducerUtils";
import {getRegTime, getUpdateStatus} from "../helpers/dataHandler";

export const FETCHED_CLIENTS_CARDS = 'FETCHED_CLIENTS_CARDS';
export const REQUESTED_CLIENTS_CARDS = 'REQUESTED_CLIENTS_CARDS';
export const REQUESTED_CLIENTS_CARDS_SUCCEEDED = 'REQUESTED_CLIENTS_CARDS_SUCCEEDED';
export const REQUESTED_CLIENTS_CARDS_FAILED = 'REQUESTED_CLIENTS_CARDS_FAILED';
export const FILTERED_CLIENTS_CARDS = 'FILTERED_CLIENTS_CARDS';
export const SELECT_CLIENTS_CARDS_STATUS = 'SELECT_CLIENTS_CARDS_STATUS';
export const REGISTER_CLIENTS_CARDS = 'REGISTER_CLIENTS_CARDS';

const initialState = {
    data: [],
    dataFiltered: [],
    changedRow: {},
    loading: false,
    error: false,
};

export default createReducer(initialState, {
    REQUESTED_CLIENTS_CARDS: requestedResourse,
    REQUESTED_CLIENTS_CARDS_SUCCEEDED: requestedClientsCardsSuccess,
    REQUESTED_CLIENTS_CARDS_FAILED: requestedError,
    FILTERED_CLIENTS_CARDS: filteredClientsCards,
    SELECT_CLIENTS_CARDS_STATUS: selectedClientsCardsStatus,
    REGISTER_CLIENTS_CARDS: registerClientsCards,
    'SELECT_STATUS_CLIENTS_VISITS': selectStatusClientsVisits,
});

// State Reducers
function requestedClientsCardsSuccess(state, action) {

    return {
        ...state,
        data: action.payload.map(item => Object.assign({}, item)),
        dataFiltered: action.payload.map(item => Object.assign({}, item)),
        loading: false,
        error: false,
    };
}

function filteredClientsCards(state, action) {

    const value = action.payload;

    let filteredCardTable = state.data.filter(item => {
        if (item['srv_id'] === +value || value === 'all') return item;
    });

    return {
        ...state,
        dataFiltered: filteredCardTable.map(item => Object.assign({}, item)),
    };
}

function selectedClientsCardsStatus(state, action) {

    const {value, row} = action.payload;

    const changedDataFiltered = state.dataFiltered.map(item => {
        if (row['client_card_id'] === item['client_card_id']) {
            return {...item, cltv_visit_status: value};
        }
        return item
    });

    return {
        ...state,
        dataFiltered: changedDataFiltered,
    };
}

function registerClientsCards(state, action) {

    const result = state.dataFiltered.map(item => {

        let modifier = 0;
        if (+item['cltv_visit_status'] !== 2 && item['cltc_remainder'] !== 0)
            modifier = item['cltc_remainder'] - 1;

        return {
            ...item,
            cltv_time: action.payload,
            cltc_remainder: modifier,
        }
    });

    return {
        ...state,
        dataFiltered: result,
    };
}

function selectStatusClientsVisits(state, action) {

    const {status, row} = action.payload;
    const modifier = getUpdateStatus(row['cltv_visit_status'], status);

    let changedRow = {};
    const result = state.dataFiltered.map(item => {
        if (row['client_card_id'] === item['client_card_id']) {
            changedRow =  {...item, cltc_remainder: item.cltc_remainder + modifier};
            return changedRow;
        }
        return item
    });

    return {
        ...state,
        dataFiltered: result,
        changedRow: changedRow,
    }
}

// Page Actions
export function fetchClientsCards() {
    return (dispatch) => {

        dispatch({type: FETCHED_CLIENTS_CARDS})
    }
}

// Action Creators
export const requestClientsCards = () => {
    return {type: REQUESTED_CLIENTS_CARDS}
};

export const requestClientsCardsSuccess = (data) => {
    return {type: REQUESTED_CLIENTS_CARDS_SUCCEEDED, payload: data}
};

export const requestClientsCardsError = () => {
    return {type: REQUESTED_CLIENTS_CARDS_FAILED}
};



export function selectClientsCardsStatus(value, row) {
    return (dispatch) => {

        dispatch({type: SELECT_CLIENTS_CARDS_STATUS, payload: {value, row}})
    }
}

export function registeredClientsCard() {
    return (dispatch, getState) => {

        const {datePicker, timeList} = getState();

        const regTime = getRegTime(datePicker.current, timeList.byDefault.label);

        dispatch({type: REGISTER_CLIENTS_CARDS, payload: regTime})
    }
}



