const HOST = 'http://localhost:3000';

export const fetchPath = (pathname) => {

    const headers = new Headers();
    headers.append('Accept', 'application/json');

    return fetch(`${HOST}${pathname}`, {
        method: 'GET',
        headers: headers
    })
        .then(res => res.json())
};

export const postPath = (pathname, data) => {

    let dataPost = new FormData();
    dataPost.append("data", JSON.stringify(data));

    return fetch(`${HOST}${pathname}`, {
        method: 'POST',
        body: dataPost,
    })
};

export const putPath = (pathname, data) => {

    let dataPost = new FormData();
    dataPost.append("data", JSON.stringify(data));

    return fetch(`${HOST}${pathname}`, {
        method: 'PUT',
        body: dataPost,
    })
};



