import React, {Component} from "react";
import {Wrapper} from "./styled"

class EditableCell extends Component {
    constructor(props) {
        super(props);

        this.inputRef = React.createRef();
        this.state = {
            isEdit: props.isEdit,
        };
    }

    handleClickOnEdit = () => {
        this.setState({
            isEdit: !this.state.isEdit,
        });
    };

    componentDidUpdate() {
        this.inputRef.current.focus();
    }

    render() {
        const {actionChangeInput, id, dataIndex} = this.props;

        return (
            <Wrapper isEdit={this.state.isEdit} onClick={this.handleClickOnWrapper}>
                <input
                    ref={this.inputRef}
                    disabled={!this.state.isEdit}
                    value={this.props.date}
                    onChange={e => actionChangeInput(id, dataIndex, e.target.value)}
                />
                <span onClick={this.handleClickOnEdit}/>
            </Wrapper>
        )
    }
}

export default EditableCell;



