import React, {Component, Fragment} from 'react'
import {Nav, StyledLink, Ul, Li} from './styled'
import {bindActionCreators} from "redux";
import * as actions from "../../../store/reducers/client-visit";
import {connect} from "react-redux";

class Admin extends Component {

    render() {
        const menuList = [
            {
                title: ' ',
                items: [
                    {
                        title: '',
                        to: '/admin/clientvisit'
                    },
                    {
                        title: ' ',
                        to: '/admin/clientinfo'
                    },
                ]
            },
            {
                title: ' ',
                items: [
                    {
                        title: '',
                        to: '/admin/schedule'
                    },
                    {
                        title: '  ',
                        to: '/admin/ticket'
                    }
                ]
            }
        ];

        return (
            <Nav>
                {menuList.map((group, gi) => {

                        const {title, items} = group;

                        return (
                            <div key={gi}>
                                <h3>{title}</h3>
                                <Ul>
                                    {items.map((item, i) =>
                                        <Li key={i}>
                                            <StyledLink active={+(this.props.location.pathname === item.to)}
                                                        to={item.to}>{item.title}
                                            </StyledLink>
                                        </Li>
                                    )}
                                </Ul>
                            </div>
                        )
                    }
                )}
            </Nav>
        );
    }
}

const mapStateToProps = (state) => state.router;
const mapDispatchToProps = (dispatch) => bindActionCreators(actions, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(Admin);



