import {createReducer, requestedError, requestedResourse} from "../helpers/reducerUtils";
import {compareTables, getRegTime, getUpdateStatus} from "../helpers/dataHandler";

export const FETCHED_CLIENTS_VISITS = 'FETCHED_CLIENTS_VISITS';
export const REQUESTED_CLIENTS_VISITS = 'REQUESTED_CLIENTS_VISITS';
export const REQUESTED_CLIENTS_VISITS_SUCCEEDED = 'REQUESTED_CLIENTS_VISITS_SUCCEEDED';
export const REQUESTED_CLIENTS_VISITS_FAILED = 'REQUESTED_CLIENTS_VISITS_FAILED';
export const REGISTER_CLIENTS_VISITS = 'REGISTER_CLIENTS_VISITS';
export const SEND_CLIENTS_VISITS = 'SEND_CLIENTS_VISITS';
export const SELECT_STATUS_CLIENTS_VISITS = 'SELECT_STATUS_CLIENTS_VISITS';

const initialState = {
    data: [],
    changedRow: {},
    loading: false,
    error: false,
};

export default createReducer(initialState, {
    REQUESTED_CLIENTS_VISITS: requestedResourse,
    REQUESTED_CLIENTS_VISITS_SUCCEEDED: requestedSuccess,
    REQUESTED_CLIENTS_VISITS_FAILED: requestedError,
    REGISTER_CLIENTS_VISITS: registerClientsVisits,
    SELECT_STATUS_CLIENTS_VISITS: selectStatusClientsVisits,
});

// State Reducers
function requestedSuccess(state, action) {

    return {
        ...state,
        data: action.payload.map(item => Object.assign({}, item)),
        loading: false,
        error: false,
    };
}

function registerClientsVisits(state, action) {

    return {
        ...state,
        data: [
            ...state.data,
            ...action.payload.map(item => ({...item}))
        ],
    }
}

function selectStatusClientsVisits(state, action) {

    const {row, status} = action.payload;

    let changedRow = {};
    const result = state.data.map(item => {
        if (row['client_card_id'] === item['client_card_id'] && row['cltv_time'] === item['cltv_time']) {
            changedRow = {...item, cltv_visit_status: status};
            return changedRow;
        }
        return item
    });

    return {
        ...state,
        data: result,
        changedRow: changedRow,
    }
}

// Page Actions
export function sendedClientsVisits() {
    return (dispatch) => {

        dispatch({type: SEND_CLIENTS_VISITS})
    }
}

export const selectClientsVisitsStatus = (status, row) => {
    return (dispatch) => {

        dispatch({type: SELECT_STATUS_CLIENTS_VISITS, payload: {status, row}})
    }
};

// Action Creators
export const requestClientsVisits = () => {
    return {type: REQUESTED_CLIENTS_VISITS}
};

export const requestClientsVisitsSuccess = (data) => {
    return {type: REQUESTED_CLIENTS_VISITS_SUCCEEDED, payload: data}
};

export const requestClientsVisitsError = () => {
    return {type: REQUESTED_CLIENTS_VISITS_FAILED}
};

export function fetchClientsVisits() {
    return (dispatch, getState) => {

        const currentDate = getState().datePicker.current;

        dispatch({type: FETCHED_CLIENTS_VISITS, payload: currentDate})
    }
}

export function checkTableEquals() {
    return (dispatch, getState) => {

        const {clientsCards, clientsVisits, datePicker, timeList} = getState();

        const regTime = getRegTime(datePicker.current, timeList.byDefault.label);

        return compareTables(clientsCards.dataFiltered, clientsVisits.data, 'client_card_id', regTime)
    }
}

export function registeredClientsVisits() {
    return (dispatch, getState) => {

        const {clientsCards} = getState();

        dispatch({type: REGISTER_CLIENTS_VISITS, payload: clientsCards.dataFiltered});
    }
}




