import React, {Component} from 'react'
import Table from '../../Table'
import Select from "../../Select";
import Input from "../../Input";
import {ButtonPrimary} from "../../Button/styled";
import {Wrapper, HandleGroup, TableWrap} from "./styled"
import moment from "moment/moment";
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import {fetchTimeList, selectTimeList} from "../../../store/reducers/timeList"
import {fetchServiceList, selectServiceList} from "../../../store/reducers/serviceList"
import {fetchClientsCards, selectClientsCardsStatus} from "../../../store/reducers/clientsCards"
import {fetchClientsVisits, sendedClientsVisits, selectClientsVisitsStatus} from "../../../store/reducers/clientsVisits"
import {selectedDate} from "../../../store/reducers/datePicker"

class ClientVisit extends Component {

    componentDidMount() {

        const {
            timeList,
            serviceList,
            clientsCards,
            clientsVisits,
            fetchTimeList,
            fetchServiceList,
            fetchClientsCards,
            fetchClientsVisits,
        } = this.props;

        serviceList.data.length === 0 && fetchServiceList();
        timeList.data.length === 0 && fetchTimeList();
        clientsCards.data.length === 0 && fetchClientsCards();
        clientsVisits.data.length === 0 && fetchClientsVisits();
    }

    statusList = [
        {
            value: '0',
            label: ' ',
        },
        {
            value: '1',
            label: '',
        },
        {
            value: '2',
            label: ' ',
        },
    ];
    columnsClientsCards = [
        {
            title: '',
            render: (column, row, row_index) => row_index + 1,
        },
        {
            title: '',
            dataIndex: 'usr_full_name',
        },
        {
            title: '',
            dataIndex: 'srv_title',
        },
        {
            title: '',
            dataIndex: 'cltc_count',
        },
        {
            title: '',
            dataIndex: 'cltc_remainder',
        },
        {
            title: '',
            dataIndex: 'is_visited',
            render: (column, row) => {
                return (
                    <Select
                        defaultSelect={row.cltv_visit_status}
                        onSelectChange={event => this.props.selectClientsCardsStatus(event.target.value, row)}
                        data={this.statusList}
                    />
                )
            }
        },
    ];
    columnsClientsVisits = [
        {
            title: '',
            render: (column, row, row_index) => row_index + 1,
        },
        {
            title: '',
            dataIndex: 'usr_full_name',
        },
        {
            title: '',
            dataIndex: 'srv_title',
        },
        {
            title: '',
            dataIndex: 'cltv_time',
            render: (column, row) => (row.cltv_time)
                ? moment(row.cltv_time, 'YYYY-MM-DD hh:mm:ss').format('hh:mm')
                : ""
        },
        {
            title: '',
            dataIndex: 'is_visited',
            render: (column, row) => {
                return (
                    <Select
                        defaultSelect={row.cltv_visit_status}
                        onSelectChange={event => {
                            this.props.selectClientsVisitsStatus(event.target.value, row);
                        }}
                        data={this.statusList}
                    />
                )
            }
        },
    ];

    render() {

        const {
            timeList,
            serviceList,
            clientsCards,
            clientsVisits,
            selectedDate,
            datePicker,
            selectServiceList,
            selectTimeList,
            sendedClientsVisits,
        } = this.props;

        return (
            <Wrapper>
                <HandleGroup>
                    <Select
                        data={serviceList.data}
                        defaultSelect={serviceList.byDefault.value}
                        onSelectChange={e => selectServiceList(e.target.value)}
                    />
                    <Select
                        data={timeList.data}
                        defaultSelect={timeList.byDefault.value}
                        onSelectChange={e => selectTimeList(e.target.value)}
                    />
                    <DatePicker
                        selected={datePicker.current}
                        dateFormat="DD-MM-YYYY"
                        onChange={value => selectedDate(value)}
                        dropdownMode="scroll"
                        customInput={<Input/>}
                    />
                    <ButtonPrimary
                        type="button"
                        onClick={() => sendedClientsVisits()}
                        value={""}
                        disabled={serviceList.byDefault.value === "all"}
                    />
                    <span>{this.props.message}</span>
                </HandleGroup>
                <TableWrap>
                    <Table
                        caption="    "
                        filterService={serviceList.byDefault.value}
                        data={clientsCards.dataFiltered}
                        columns={this.columnsClientsCards}
                    />
                    <Table
                        caption={"   "}
                        date={datePicker.current.format("DD-MM-YYYY")}
                        filterService={serviceList.byDefault.value}
                        data={clientsVisits.data}
                        columns={this.columnsClientsVisits}
                    />
                </TableWrap>
            </Wrapper>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        timeList: state.timeList,
        serviceList: state.serviceList,
        clientsCards: state.clientsCards,
        clientsVisits: state.clientsVisits,
        clientVisit: state.clientVisit,
        datePicker: state.datePicker
    };
};
const mapDispatchToProps = (dispatch) => bindActionCreators({
    fetchTimeList,
    fetchServiceList,
    fetchClientsCards,
    fetchClientsVisits,
    selectedDate,
    selectServiceList,
    selectTimeList,
    selectClientsCardsStatus,
    selectClientsVisitsStatus,
    sendedClientsVisits,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(ClientVisit);



