import styled from 'styled-components';

export const Button = styled.input`

    height: 2.5em;
    padding: .375em .825em;
    border: 1px solid #00ba9b;
    border-radius: 4px;
    color: white;
    font-size: 1em;
    line-height: 1.5em;

    &:hover {
      border-color: #aeaeae;
      cursor: pointer;
    }
    
    &:disabled {
      border-color: #aeaeae;
      background-color: #aeaeae;
    }
`;

export const ButtonPrimary = styled(Button)`

      background-color: #00d1b2;
      color: white;
      
      &:hover {
        border-color: #00d1b2;
        background-color: #00ba9b;
      }
      
      &:disabled {
      color: #ffffff;
      border-color: #8bc4b2;
      background-color: #8bc4b2;
      cursor: default;
      }
`;



