import moment from "moment";

export function compareTablesByFields(tableOne, tableTwo, fields) {

    let limit = fields.length;

    if (tableTwo.length === 0) return false;

    return tableOne.some((rowOne) => {

        return tableTwo.some((rowTwo) => {

            let compares = 0;
            return fields.some((triger) => {

                if (rowOne[triger] === rowTwo[triger]) ++compares;
                if (limit === compares) return true;
            });
        });
    });
}

export function compareTables(tableOne, tableTwo, id, time) {

    if (tableOne.length === 0 || tableTwo.length === 0) return false;

    return tableOne.some((rowOne) => {

        return tableTwo.some((rowTwo) => {

            if (rowOne[id] === rowTwo[id] && rowTwo['cltv_time'] === time) return true;
        });
    });
}

export function getRegTime(date, time) {
    const days = date.format("YYYY-MM-DD");
    const hours = moment(time, 'hh:mm').format('hh:mm:ss');

    return `${days} ${hours}`;
}

export function getUpdateStatus(prevStatus, nextStatus) {

    // console.log('prevStatus, nextStatus', prevStatus, nextStatus);

    const stateArr = [
        ["0", "1", 0],
        ["0", "2", 1],
        ["2", "0", -1],
        ["1", "0", 0],
        ["1", "2", 1],
        ["2", "1", -1],
    ];

    let state = 0;

    stateArr.forEach(item => {
        if (item[0] === String(prevStatus) && item[1] === String(nextStatus)) state = item[2];
    });

    return state;
}



