import styled from 'styled-components';
import {Link} from 'react-router-dom';

export const Nav = styled.nav`
  
  margin-right: 80px;

  h3 {
    color: #393939;
    margin-top: 0;
    margin-bottom: 10px;
    font-weight: 500;
  }
  
  & > div:nth-child(n+2) {
    margin-top: 20px;
  }
`;

export const Ul = styled.ul`
    list-style: none;
    margin: 0;
    padding-left: 15px;
`;

export const Li = styled.li`
    margin: 5px 0;
`;

export const StyledLink = styled(Link)`
    text-decoration: none;
    color: ${props => props.active ? '#6b00b1' : '#717171'};
    
    &:hover,
    &:active{
        color: #6b00b1;
    }
`;



