import {createReducer, requestedError, requestedResourse} from "../helpers/reducerUtils";

const REQUESTED_TIME_LIST = 'REQUESTED_TIME_LIST';
const REQUESTED_TIME_LIST_SUCCEEDED = 'REQUESTED_TIME_LIST_SUCCEEDED';
const REQUESTED_TIME_LIST_FAILED = 'REQUESTED_TIME_LIST_FAILED';
const FETCHED_TIME_LIST = 'FETCHED_TIME_LIST';
const SELECT_TIME_LIST = 'SELECT_TIME_LIST';

const initialState = {
    data: [],
    byDefault: {},
    loading: false,
    error: false,
};

export default createReducer(initialState, {
    REQUESTED_TIME_LIST: requestedResourse,
    REQUESTED_TIME_LIST_SUCCEEDED: requestedTimeListSuccess,
    REQUESTED_TIME_LIST_FAILED: requestedError,
    SELECT_TIME_LIST: selectedTimeList,
});

// State Reducers
function requestedTimeListSuccess(state, action) {

    const data = action.payload.map(item => {
        return {
            value: item.id,
            label: item.title
        }
    });

    return {
        ...state,
        data: data,
        byDefault: data[0],
        loading: false,
        error: false,
    };
}

function selectedTimeList(state, action) {

    let result = {};
    for(let item of state.data) {
        if(item.value === +action.payload) result = item;
    }

    return {
        ...state,
        byDefault: result,
    };
}

// Page Actions
export function fetchTimeList() {
    return (dispatch) => {
        dispatch({type: FETCHED_TIME_LIST})
    }
}

// Action Creators
export const requestTimeList = () => {
    return {type: REQUESTED_TIME_LIST}
};

export const requestTimeListSuccess = (data) => {
    return {type: REQUESTED_TIME_LIST_SUCCEEDED, payload: data}
};

export const requestTimeListError = () => {
    return {type: REQUESTED_TIME_LIST_FAILED}
};



export function selectTimeList(value) {
    return (dispatch) => {
        dispatch({type: SELECT_TIME_LIST, payload: value})
    }
}



