import React from 'react'
import PropTypes from "prop-types";
import {Wrapper, SelectStyled} from './styled'

const Select = ({data, defaultSelect, onSelectChange}) => {

    return (
        <Wrapper>
            <SelectStyled value={defaultSelect} onChange={onSelectChange}>
                {data.map(item =>
                    <option key={item.value} {...item}/>
                )}
            </SelectStyled>
        </Wrapper>
    )
};

Select.propTypes = {
    data: PropTypes.array,
    defaultSelect: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number,
    ]),
    onSelectChange: PropTypes.func,
};

Select.defaultProps = {
    data: [],
    onSelectChange: () => {
        console.log('onFilterSelectChange defaultProp')
    },
};

export default Select;



