import React from 'react'
import PropTypes from "prop-types";
import Row from './Row'
import {TableStyled} from './styled'

const Table = ({data, columns, onSelectRow, caption, date, className}) => {
    const header = columns.map((item, i) => {
        return <th key={i}>{item.title}</th>
    });
    const body = data.map((row, i) => {
        let crossed = renderColumns(row, columns, i);
        return <Row key={i} data={data} crossed={crossed} rowIndex={i} onSelectRow={onSelectRow}/>
    });

    return (
        <TableStyled className={className}>
            <caption>{caption}<b>{date}</b></caption>
            <thead>
            <tr>
                {header}
            </tr>
            </thead>
            <tbody>
            {body}
            {data.length === 0 && <tr>
                <td colSpan={columns.length} style={{paddingTop: "30px", paddingBottom: "30px"}}> </td>
            </tr>}
            </tbody>
        </TableStyled>
    )
};

Table.propTypes = {
    data: PropTypes.array,
    date: PropTypes.string,
    caption: PropTypes.string,
    columns: PropTypes.array,
    filterService: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number,
    ]),
};

Table.defaultProps = {
    date: '',
};

function renderColumns(row, columns, row_index) {

    let newobj = {};
    columns.forEach(function (column) {
        newobj[column.dataIndex] = column.render ? column.render(column, row, row_index) : row[column.dataIndex];
    });
    return newobj
}

export default Table;



