import {call, put, select, takeEvery} from 'redux-saga/effects'
import {
    requestClientsVisits,
    requestClientsVisitsSuccess,
    requestClientsVisitsError,
    checkTableEquals,
    registeredClientsVisits,
} from '../reducers/clientsVisits'
import {registeredClientsCard} from '../reducers/clientsCards'
import * as Api from '../Api'
import {messagePutError} from "../reducers/message";
import clientsCards from "../reducers/clientsCards";

// Sagas
export function* watchFetchClientVisits() {
    yield takeEvery(['FETCHED_CLIENTS_VISITS', 'SELECT_DATE'], fetchAsync);
    yield takeEvery(['SEND_CLIENTS_VISITS'], sendAsync);
    yield takeEvery(['SELECT_STATUS_CLIENTS_VISITS'], selectStatusAsync);
}

// Worker Saga
function* fetchAsync(action) {
    try {

        yield put(requestClientsVisits());

        const data = yield call(Api.fetchPath, `/admin/clientvisit?date=${action.payload.format("YYYY-MM-DD")}`);

        yield put(requestClientsVisitsSuccess(data));

    } catch (error) {

        yield put(requestClientsVisitsError());
    }
}

function* sendAsync() {
    try {

        const isEqual = yield put(checkTableEquals());

        if (!isEqual) {
            yield put(registeredClientsCard());
            yield put(registeredClientsVisits());
            const data = select().clientsCards.dataFiltered;

            yield call(Api.postPath, '/admin/clientvisit', data);

        } else {
            yield put(messagePutError('   ,  '));
        }

    } catch (error) {

        yield put(requestClientsVisitsError());
    }
}

function* selectStatusAsync() {
    try {
        const state = yield select();

        yield call(Api.putPath, '/admin/clientvisit/status', {
            clientsVisits: [state.clientsVisits.changedRow],
            clientsCards: [state.clientsCards.changedRow],
        });

    } catch (error) {

        yield put(messagePutError('selectStatusAsync Error:' + error));
    }
}



