const ACTION_INIT_USER = 'ACTION_INIT_USER';
const ACTION_IS_GUEST = 'ACTION_IS_GUEST';
const ACTION_LOGOUT = 'ACTION_LOGOUT';
const SHOW_ERRORS = 'SHOW_ERRORS';
const ADD_NEW_USER = 'ADD_NEW_USER';

const initialState = {
    userGroup: "3",
    userIsGuest: true,
    userIsAdmin: false,
    errors: {},
    newUser: {},
};

export default function reducer(state = initialState, action) {
    switch (action.type) {
        case ACTION_INIT_USER:
            return {
                ...state,
                userGroup: action.payload,
                userIsGuest: false,
                userIsAdmin: action.payload === "1",
            };
        case ACTION_LOGOUT:
            return {
                ...state,
                ...initialState,
            };
        case SHOW_ERRORS:
            return {
                ...state,
                errors: action.payload,
            };
        case ADD_NEW_USER:
            return {
                ...state,
                newUser: action.payload,
            };
        default:
            return state;
    }
}

function actionInitUser(userGroup) {
    return {
        type: ACTION_INIT_USER,
        payload: userGroup,
    }
}

function actionShowErrors(errors) {
    return {
        type: SHOW_ERRORS,
        payload: errors,
    }
}

function actionLogout() {
    return {
        type: ACTION_LOGOUT,
    }
}

export function actionAddNewUser(newUser) {
    // console.log('actionAddNewUser', newUser);
    return {
        type: ADD_NEW_USER,
        payload: newUser,
    }
}

export function requestInitUser() {
    return (dispatch) => {
        fetch("http://localhost/", {credentials: "include"})
            .then(res => res.json())
            .then(data => {
                // console.log('requestUserIsGuest', data);
                if (typeof data !== "boolean") dispatch(actionInitUser(data))
            })
            .catch(err => {
                console.log('Fetch Error:', err);
            })
    }
}

export function sendAuthForm(state) {
    let postData = new FormData();
    postData.append('login', state.login);
    postData.append('password', state.password);

    return (dispatch) => {
        fetch("http://localhost/site/login", {
            method: 'post',
            body: postData,
            credentials: "include",
        })
            .then(res => res.json())
            .then(data => {
                console.log(data);
                if (data.hasOwnProperty('errors')) dispatch(actionShowErrors(data.errors));
                if (data.hasOwnProperty('group_access')) dispatch(actionInitUser(data.group_access));
            })
            .catch(err => {
                console.log('Fetch Error:', err);
            })
    }
}

export function sendRegForm(state, userLogin = true) {
    let postData = new FormData();
    for (let key in state) {
        postData.append(key, state[key]);
    }

    return (dispatch) => {
        fetch("http://localhost/site/registration", {
            method: 'post',
            body: postData,
            credentials: "include",
        })
            .then(res => res.json())
            .then(data => {
                // console.log(data);
                if (data.hasOwnProperty('user')) dispatch(actionAddNewUser(
                    {
                        usr_email: data.user['email'],
                        usr_full_name: data.user['full_name'],
                        usr_id: data.user['id'],
                        usr_login: data.user['login'],
                        usr_money: data.user['money']
                    }
                ));
                if (data.hasOwnProperty('group_access') && userLogin) dispatch(actionInitUser(data.group_access));
                if (data.hasOwnProperty('errors')) dispatch(actionShowErrors(data.errors));
            })
            .catch(err => {
                console.log('Fetch Error:', err);
            })
    }
}


