import styled from 'styled-components';


export const Wrapper = styled.div`
    display: inline-block;
    max-width: 100%;
    position: relative;
    vertical-align: top;
    height: 100%;
    
    &::after {
        border: 3px solid #dbdbdb;
        border-radius: 2px;
        border-right: 0;
        border-top: 0;
        box-sizing: border-box;
        content: "";
        top: calc(50% - 2px);
        right: 0.5em;
        position: absolute;
        display: block;
        height: .7em;
        width: .7em;
        pointer-events: none;
        transform: rotate(-45deg) translateY(-50%);
        transform-origin: center;
    }
    
    &:hover::after {
        border-color: #868686;
    }
    
        

`;

export const SelectStyled = styled.select`
    height: 2.5em;
    background-color: transparent;
    border-color: #dbdbdb;
    border-radius: 4px;
    color: #363636;
    cursor: pointer;
    display: block;
    font-size: 1em;
    line-height: 1.5em;
    max-width: 100%;
    outline: 0;
    padding: calc(.375em - 1px) calc(.625em - 1px);
    padding-right: 2.2em;
    -moz-appearance: none;
    -webkit-appearance: none;
    
    &:focus {
        box-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
    }
`;



